.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 25s linear;
          animation: App-logo-spin infinite 25s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(74, 11, 110);
  height: 120px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.gitAvatar {
  width: 150px;
  display: block;
  margin: auto;
  border-radius: 90px;
}
